import React from 'react';
import CombinedTable from './combined-table.component'

const DirectorTable = ({itemArray, deleteItem, ...otherProps}) => {
    console.log("itemArray: ", itemArray);
    itemArray = [
        {
            id: 1, title: "Mr", first_name: "John", last_name: "Doe", alias: "Big Boy", ssn: 123
        }
    ]
    const fields = ['id', 'title','first_name', 'last_name', 'alias', 'ssn']

    return(
        <CombinedTable name="Directors Table" fields={fields} tableData={itemArray} />
    )
}

export default DirectorTable;