import ReportPersonTypes from '../transaction-person/report-person.types';


export const RetrieveTransactionFromReport = (fullReport, internal_ref_number) => {
    const existingTxn = fullReport.transaction.find(
        reportTransaction => reportTransaction.internal_ref_number === internal_ref_number
    );

    return existingTxn;
}

export const RetrieveFromReportTFrom = (fullReport, internal_ref_number) => {
    const existingTxn = fullReport.transaction.find(
        reportTransaction => reportTransaction.internal_ref_number === internal_ref_number
    );

    if(!existingTxn)
        return null;
        
    return existingTxn.t_from;
}

