import React, { useEffect } from 'react';
import { useSelector } from 'react-redux';
import CombinedTable from './combined-table.component';

const PersonTable = ({ itemArray, deleteItem, ...otherProps }) => {

    const fields = ['title', 'first_name', 'last_name', 'alias', 'ssn'];
    var tableData =[];

    if(itemArray){
        
        console.log("itemPerson: ", itemArray);
        tableData.push(itemArray);
    }

    return(
        <CombinedTable name="Person Table" fields={fields} tableData={tableData} deleteItem={deleteItem} />
    )
}

export default PersonTable;
